.category-catalog__item {
  background-color: @grey-100;
}

.category-catalog__title {
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}

.category-catalog__text {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: @dark-grey-100;
}

.category-catalog__link {
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  color: @basic-black;
  text-decoration: none;
  text-transform: uppercase;
}
